
    import {
        defineComponent,
        reactive,
        onMounted
    } from "vue";
    import {
        apiRoleLists,
        apiRoleDel
    } from '@/api/auth'
    import Pagination from '@/components/pagination/index.vue'
    import Popup from '@/components/popup/index.vue'
    import {
        usePages
    } from '@/core/hooks/pages'
    export default defineComponent({
        components: {
            Pagination,
            Popup,
        },
        setup() {
            // 表单数据
            const formData = reactive({
                id: 0, // 角色id
                name: '', // 角色名称
                desc: '', // 备注
                create_time: '', // 创建时间
                num: 0, // 使用该角色的人数
            })

            const {
                pager,
                requestApi
            } = usePages()
            const getRoleLists = () => {
                requestApi(apiRoleLists, formData)
            }

            // 删除角色
            const handleDelete = (id: number) => {
                apiRoleDel({
                        id
                    })
                    .then((res: any) => {
                        console.log('res', res)
                        getRoleLists()
                    })
                    .catch((err: any) => {
                        console.log('err', err)
                    })
            }

            onMounted(() => {
                getRoleLists()
            })

            return {
                formData,
                apiRoleLists,
                handleDelete,
                pager,
                getRoleLists
            }
        },
    })
